<template>
    <div>
      <ul style="list-style-type: none;" class="collection with-header">
        <div style= "background-color: #5597b4;" class="collection-header"><h3 style="color: white;">Contatos</h3></div>
        <table>
          <thead>
            <tr>
              <td style="background-color:#ECE9D8; " class=" collection-item">
                <b>Primeiro Nome</b>
              </td>
              <td style="background-color:#ECE9D8;" class="collection-item">
                <b>Ultimo Nome</b>
              </td>
              <td style="background-color:#ECE9D8;" class="collection-item">
                <b>Telemovel</b>
              </td>
              <td style="background-color:#ECE9D8" class="collection-item">
                <b>Ver</b>
              </td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="contato in contatos" v-bind:key="contato.id">
              <td class="collection-item">
                {{contato.primeiroNome}}
              </td>
              <td class="collection-item">
                {{contato.ultimoNome}}
              </td>
              <td class="collection-item">
                {{contato.telemovel}}
              </td>
              <router-link class="collection-item" v-bind:to="{name: 'view-contato', params: {contato_id: contato.contato_id}}">
                <i class="fa fa-eye" style="color:#70AB8F"></i>
              </router-link>
            </tr>
          </tbody>
        </table>


        
      </ul>
      <div class="fixed-action-btn">
        <!-- botao com link para criação de novo contato -->
        <router-link to="/novo_cont" class="btn-floating" style="background-color:#ffcd00;">
          <i class="fa fa-plus"></i>
        </router-link>
      </div>
    </div>
</template>

<script>
import firebase from "./firebaseInit";
export default {
  name: "Contatos",
  data() {
    return {
      contatos: []
    };
  },
  created() {
    firebase
      .collection("contatos")
      .orderBy("primeiroNome")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(element => {
          console.log(element.data());
          console.log(element.id);
          console.log(element.telemovel);
          const data = {
            id: element.id,
            contato_id: element.data().contato_id,
            primeiroNome: element.data().primeiroNome,
            ultimoNome: element.data().ultimoNome,
            morada: element.data().morada,
            telemovel: element.data().telemovel
          };
          this.contatos.push(data);
        });
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
th, td {
  min-width: 120px;
  padding: 10px 20px;
  text-align: center;
}
li {
  background-color: #5597b4;
}

</style>



